import React, { Component } from 'react';
import { Button, Row, Col, Typography } from 'antd';
import SuperFetch from '../../helpers/superFetch';
import notification from '@isomorphic/shared/isomorphic/components/Notification';
import XLSX from 'xlsx';
import ReactJson from 'react-json-view'
import { Chart } from "react-google-charts";
import moment from 'moment';
//http://jsfiddle.net/asgallant/R8A8P/
//https://developers.google.com/chart/interactive/docs/printing
export default class extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jsonData: [],
      data:null,
      piedata:null,
      gauge:[],
    }
    this.generateChart = this.generateChart.bind(this);
  }
  generateCSV = e => {
    SuperFetch.get('report/csv')
    .then(response => {
      console.log('response', response)
      if(response.status === 200) {
        var ws = XLSX.utils.json_to_sheet(response.data);
        var wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, 'report');
        XLSX.writeFile(wb, 'report.xlsb');
        //console.log(response)
      } else {
        notification('error', "Something wrong in retrieving CSV" );
      }
    })
    .catch(e => console.log(e))
  }

  generateJSON = e => {
    SuperFetch.get('report/json')
    .then(response => {
      console.log('response', response)
      if(response.status === 200) {
        this.setState({
          jsonData:response.data
        });
      } else {
        notification('error', "Something wrong in retrieving CSV" );
      }
    })
    .catch(e => console.log(e))
  }
  generateChart = e => {
    SuperFetch.get('report/claimCount')
    .then(response => {
      console.log('response', response)
      if(response.status === 200) {

        var table = response.data.map(item => [moment(item.month).format('MM-YYYY'), item.prCount, item.cpCount])
        table.unshift(['Month', 'pr Count', 'cp Count'])


        this.setState({
          data:table
        });
      } else {
        notification('error', "Something wrong in retrieving CSV" );
      }
    })
    .catch(e => console.log(e))
  }
  approvedAmount = e => {

    //retrieve from original data
    SuperFetch.get('report/USDApproved')
    .then(response => {
      console.log('response', response)
      if(response.status === 200) {

        var table = response.data.map(claim => {
          return {
            month:moment(claim.createdAt).format('MM-YYYY'),
            approved:claim.ClaimInfoVisits.reduce((acc, curr) => {
              //Get total approved from each visit
              var billingApprovedTotal = curr.BillingInfos.reduce((accBill,currBill) => (accBill + currBill.approved), 0)
              //Get acc and convert to USD
              acc += billingApprovedTotal/curr.billingRate
              return acc;
            }, 0)}
        })
        var tableByMonth = table.reduce((acc, obj) => {

          var index = acc.findIndex(element => element[0] === obj.month)
          if (index < 0) {
            acc.push([obj.month, obj.approved]);
          } else {
            acc[index][1] += obj.approved;
          }

          return acc;
        }, [])
        tableByMonth.unshift(['Month', 'USD total'])
        this.setState({
          data:tableByMonth
        });
      } else {
        notification('error', "Something wrong in retrieving CSV" );
      }
    })
    .catch(e => console.log(e))
  }
  rejectedAmount = e => {

    //retrieve from original data
    SuperFetch.get('report/USDRejected')
    .then(response => {
      console.log('response', response)
      if(response.status === 200) {
        var table = response.data.map(claim => {
          return {
            month:moment(claim.createdAt).format('MM-YYYY'),
            rejected:claim.ClaimInfoVisits.reduce((acc, curr) => {
              //Get total approved from each visit
              var billingRejectedTotal = curr.BillingInfos.reduce((accBill,currBill) => (accBill + (currBill.value-currBill.approved)), 0)
              //Get acc and convert to USD
              acc += billingRejectedTotal/curr.billingRate
              return acc;
            }, 0)}
        })

        var tableByMonth = table.reduce((acc, obj) => {

          var index = acc.findIndex(element => element[0] === obj.month)
          if (index < 0) {
            acc.push([obj.month, obj.rejected]);
          } else {
            acc[index][1] += obj.rejected;
          }

          return acc;
        }, [])
        tableByMonth.unshift(['Month', 'USD total'])
        this.setState({
          data:tableByMonth
        });
      } else {
        notification('error', "Something wrong in retrieving CSV" );
      }
    })
    .catch(e => console.log(e))
  }
  countByValue = e => {

    //retrieve from original data
    SuperFetch.get('report/USDApproved')
    .then(response => {
      console.log('response', response)
      if(response.status === 200) {

        var table = response.data.map(claim => {
          return {
            approved:claim.ClaimInfoVisits.reduce((acc, curr) => {
              //Get total approved from each visit
              var billingApprovedTotal = curr.BillingInfos.reduce((accBill,currBill) => (accBill + currBill.approved), 0)
              //Get acc and convert to USD
              acc += billingApprovedTotal/curr.billingRate
              return acc;
            }, 0)}
        })
        const total = table.length;
        const greater = table.filter(item => item.approved>250).length

        var data = []
        data.push(['Amount','Count'])
        data.push(['<=$250', total-greater])
        data.push(['>$250', greater])

        this.setState({
          piedata:data
        });
      } else {
        notification('error', "Something wrong in retrieving CSV" );
      }
    })
    .catch(e => console.log(e))
  }

  countByType = e => {

    //retrieve from original data
    SuperFetch.get('report/approvedClaimedCountByType')
    .then(response => {
      if(response.status === 200) {

        const data = response.data.map(item => [item.policyType,item.policyTypeCount])
        data.unshift(['Type','Count'])
        console.log(data)
        this.setState({
          piedata:data
        });
      } else {
        notification('error', "Something wrong in retrieving CSV" );
      }
    })
    .catch(e => console.log(e))
  }

  test = e => {

    //retrieve from original data
    SuperFetch.get('report/turnaroundTime')
    .then(response => {
      console.log('response', response)
      if(response.status === 200) {

        const average = response.data.reduce((acc, curr) => acc += moment(curr.updatedAt).diff(moment(curr.createdAt), 'days'), 0)/response.data.length
        const perDay = response.data.length/90

        this.setState({
          gauge: [
            ['Label', 'Value'],
            ['Turnaround', average],
            ['Per Day', perDay],
          ]
        });
      } else {
        notification('error', "Something wrong in retrieving CSV" );
      }
    })
    .catch(e => console.log(e))
  }
  render() {
    return (
      <>
        <Row>
          <Col span={4}>
            <Button type="primary" onClick={this.generateCSV}>Excel</Button>
          </Col>
          <Col span={4}>
            <Button type="primary" onClick={this.generateJSON}>Json</Button>
          </Col>
          <Col span={4}>
            <Button type="primary" onClick={this.generateChart}>Chart</Button>
          </Col>
          <Col span={4}>
            <Button type="primary" onClick={this.approvedAmount}>USD Approved</Button>
          </Col>
          <Col span={4}>
            <Button type="primary" onClick={this.rejectedAmount}>USD Rejected</Button>
          </Col>
          <Col span={4}>
            <Button type="primary" onClick={this.countByValue}>Approved Claims By value</Button>
          </Col>
          <Col span={4}>
            <Button type="primary" onClick={this.countByType}>Approved Claims By Type</Button>
          </Col>
          <Col span={4}>
            <Button type="primary" onClick={this.test}>Test</Button>
          </Col>
        </Row>
        <Row>
          <Typography.Title level={4}>JSON:</Typography.Title>
          <ReactJson src={this.state.jsonData}/>
        </Row>
        <Row>
          <Typography.Title level={4}>Chart:</Typography.Title>
          <Chart
            width={300}
            height={300}
            chartType="ColumnChart"
            loader={<div>Loading Chart</div>}
            data={this.state.data}
            options={{
              title: 'Claims Count',
              chartArea: { width: '30%' },
              hAxis: {
                title: 'Month',
                minValue: 0,
              },
              vAxis: {
                title: 'Count',
              },
            }}
            legendToggle
          />
          <Chart
            width={400}
            height={120}
            chartType="Gauge"
            loader={<div>Loading Chart</div>}
            data={this.state.gauge}
            options={{
              redFrom: 90,
              redTo: 100,
              yellowFrom: 75,
              yellowTo: 90,
              minorTicks: 5,
            }}
          />
          <Chart
            width={300}
            height={300}
            chartType="PieChart"
            loader={<div>Loading Chart</div>}
            data={this.state.piedata}
            options={{
              title: 'Approved Claims Count: Split by Value',
              // Just add this option
              is3D: true,
            }}
            legendToggle
          />

        </Row>
      </>
    );
  }
}
